.page-container {
  width: 100% !important;
  margin: 0;
  max-width: 100%;
}

.editing-page {
  display: block;
  justify-content: flex-end;
  align-items: center;
  margin-left: 30vw;
  width: 70vw;
  max-width: 70vw!important;
  padding: 0;
}

.page-section {
  align-items: center;
  justify-content: center;
  display: flex;
  z-index: 1;
}

.page-section:hover {
  border: 2px solid #6c5ce7;
}

.clicked {
  border: 2px solid #81ecec;
}

.editor-menu {
  position: absolute;
  z-index: 2;
  bottom: 2%;
  right: 1%;
  width: 20%;
}

.toast-header {
  font-size: large;
}

.toast-body {
  font-size: medium;
}